:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
  --button-black: #3a3937;
  --button-white: #fff7e6;
}

.base-button {
  background: var(--secondary);
  outline: 0;
  text-transform: capitalize;
  color: rgba(var(--primary), 0.8);
  font-family: Montserrat-SemiBold;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 25px;
  padding-left: 25px;
  border-radius: 15px;
  text-decoration: none;
  font-size: 18px;
  cursor: pointer;
  border: 2px solid var(--secondary);
  transition: background 0.2s ease-out, color 0.2s ease-out;
}
.base-button:hover {
  background: transparent;
  color: var(--secondary);
}
.base-button.black-button {
  background: var(--button-black);
  color: var(--secondary);
  border-color: var(--button-black);
}
.base-button.black-button:hover {
  background: transparent;
}
.base-button.white-button {
  background: var(--button-white);
  color: var(--primary);
  border-color: var(--button-white);
}
.base-button.white-button:hover {
  background: transparent;
  color: var(--button-white);
}
.base-button > .icon {
  margin-right: 4px;
}

:root {
  --primary: #2b2a28;
