  --accent: #fff7e6;
  --button-black: #3a3937;
  --button-white: #fff7e6;
}

hr {
  background-color: var(--accent);
  border: 0;
  height: 1px;
  margin-top: 20.25px;
  margin-bottom: 25px;
}

.connect {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.connect p {
  font-family: "Montserrat-SemiBold", sans-serif;
}
.connect a {
  text-decoration: none;
}
.connect article {
  display: flex;
  align-items: center;
}
.connect article img {
  margin-right: 16px;
}
@media (min-width: 1024px) {
  .connect article img {
    margin-right: 12.25px;
  }
}
.connect .phone {
  margin-bottom: 16px;
  margin-top: 36px;
  margin-left: -112px;
}
@media (min-width: 1024px) {
  .connect .phone {
    margin-top: 0px;
  }
}
.connect .email {
  text-decoration: underline;
}
.connect .email a:nth-of-type(2) {
  text-decoration-color: var(--accent);
}

.copyright-text {
  text-align: center;
  font-style: italic;
}

.logo-section, .working-hours, .social-icons {
  display: none;
}

@media (min-width: 1024px) {
  footer {
    width: 95%;
    margin: 0 auto;
  }

  .first-part {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .logo-section {
    display: block;
  }

  .working-hours {
    display: flex;
    align-items: center;
    width: 50%;
    justify-content: space-between;
  }
  .working-hours p {
    font-size: 26px;
    text-align: center;
  }
  .working-hours:first-child {
    font-weight: bold;
  }

  .social-icons {
    display: block;
    text-align: center;
    margin-bottom: 28px;
  }
  .social-icons img {
    margin-right: 21px;
    height: 52px;
    width: 52px;
  }
  .social-icons img:last-child {
    margin-right: 0;
  }
}
body {
  background: var(--primary);
}